.Calendar {
  color: #333333;

  .row {

    margin: 0;
    padding: 0;
    display: flex;

    .day {

      list-style: none;
      flex: 1;
      text-align: center;
      height: 32px;
      margin: 2px .5rem;
      display: flex;
      justify-content: center;
      align-items: center;

      &.disabled,
      &.disabled * {
        pointer-events: none;
        color: #bbbbbb;
      }

      &.today {
        border: 1px solid #dddddd;
      }

      &.active {
        background-color: var(--ion-color-primary);
        color: white;
        border-radius: 2px;
      }
    }
  }
}

.Calendar__header {
  .week-list {
    display: flex;

    .item {
      flex: 1;
      text-align: center;
    }
  }

  padding-bottom: .75rem;
  border-bottom: 1px solid #f2f2f2;
}

.Clendar__title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
  height: 32px;
  margin-right: -5px;
  margin-left: -5px;

  .title {
    font-size: 16px;
    font-weight: 500;
  }

  .next,
  .prev {
    flex: 0 0 32px;
    width: 32px;
    height: 32px;
    position: relative;

    &::after {
      content: "";
      position: absolute;
      top: 10px;
      height: 30%;
      width: 30%;
      border-width: 1px;
      border-style: solid;
      border-color: var(--ion-color-primary);
    }

  }

  .prev::after {
    left: 12px;
    border-right: transparent;
    border-top: transparent;
    transform: rotate(45deg);
  }

  .next::after {
    right: 12px;
    border-right: transparent;
    border-top: transparent;
    transform: rotate(-135deg);
  }
}

.CalendarPicker__input {
  color: #333;
  border: 0;
  text-align: right;
  outline: none;
  padding: 10px 10px 10px 20px;
  line-height: 1;
  min-width: 100px;
}